@media (max-width: 749px) {
	$k: 0.48;
	$smw: 360px;
	html {
		min-width: 360px; }
	body {
		font: #{$base-fz * $k}/#{$base-lh} $font; }
	.container {
		width: $smw;
		margin: 0 auto;
		position: relative;
		&.camera {
			padding: 0; }
		&.checkout {
			padding-top: 199px * $k; }
		&.home {
			padding: 0 20px * $k 20px * $k; } }
	.ptop {
		padding-top: 128px * $k; }
	.content {}

	.wrapper {}

	.inputbox, textarea, input[type="text"], input[type="password"], input[type="datetime"], input[type="datetime-local"], input[type="date"], input[type="month"], input[type="time"], input[type="week"], input[type="number"], input[type="email"], input[type="url"], input[type="search"], input[type="tel"], input[type="color"] {
		padding: 20px * $k 0 18px * $k;
		height: 76px * $k;
		font-size: 30px * $k; }

	.splash_logo {
		width: 205px * $k;
		height: 91px * $k;
		.logo {
			width: 181px * $k; }
		.dots {
			width: 15px * $k;
			margin-left: 7px * $k;
			margin-top: 16px * $k; } }
	.main_logo {
		width: 205px * $k;
		height: 91px * $k;
		.logo {
			width: 181px * $k; }
		.dots {
			width: 15px * $k;
			margin-left: 7px * $k;
			margin-top: 16px * $k; } }


	.input_wrap {
		.inputbox {
			padding-right: 40px * $k; }
		label {
			top: 20px * $k;
			font-size: 30px * $k; }
		.inputbox:focus+label, .inputbox.not_empty+label {
			line-height: 20px * $k;
			font-size: 18px * $k; }
		&.success {
			&:after {
				top: 20px * $k; } }
		&.error {
			&:after {
				top: 20px * $k; }
			label {
				color: $red; } } }
	.button {
		padding: 0 50px * $k 9px * $k;
		height: 100px * $k;
		font-size: 36px * $k;
		@include rad(50px * $k);
		i.arr {
			margin-left: 9px * $k;
			height: 24px * $k;
			width: 13px * $k; } }

	.form_list {
		li {
			margin-bottom: 32px * $k; } }

	.login_form {
		margin: 122px * $k 20px * $k 0 20px * $k;
		padding: 0 60px * $k 60px * $k;
		.inputbox {
			line-height: 50px * $k;
			padding: 14px * $k 40px * $k 26px * $k 76px * $k;
			height: 91px * $k; }
		.input_wrap {
			label {
				left: 76px * $k;
				top: 14px * $k;
				line-height: 50px * $k; } }
		.input_user {
			&:before {
				font-size: 50px * $k;
				top: 12px * $k;
				width:  50px * $k + 1; } }
		.input_pass {
			&:before {
				font-size: 50px * $k;
				top: 12px * $k;
				width:  50px * $k + 1; } }
		.form_list {
			li {
				margin-bottom: 16px * $k; }
			.buttons {
				margin-top: 60px * $k; } } }

	.form_links {
		margin: 48px * $k 0 0;
		li+li {
			margin-top: 44px * $k; } }


	.checkout_form_done {
		width: 238px * $k;
		height: 238px * $k;
		border: 10px * $k solid rgba($green,0.6);
		&:before {
			font-size: 67px * $k;
			line-height: 227px * $k; } }

	.register_form {
		margin: 64px * $k 40px * $k 0;
		.jus {
			.input_wrap {
				width: 303px * $k; }
			.input_at {
				font-size: 30px * $k;
				padding: 20px * $k 0 18px * $k; } }
		.buttons {
			margin-top: 60px * $k; }
		.button {
			width: 510px * $k; } }

	.select_wrap {
		&:before {
			font-size: 12px * $k;
			right: 7px * $k;
			top: 37px * $k; } }
	.photo_header {
		height: 88px * $k;
		padding: 23px * $k 40px * $k; }
	.camera_switch {
		margin-right: 76px * $k;
		width: 56px * $k;
		height: 42px * $k;
		&:before {
			font-size: 42px * $k; } }
	.camera_flash {
		width: 40px * $k;
		height: 42px * $k;
		&:before {
			font-size: 40px * $k;
			line-height: 42px * $k; } }
	.camera_close {
		&:before {
			font-size: 44px * $k;
			line-height: 42px * $k; } }
	.camera_tools {
		height: 203px * $k;
		padding: 49px * $k 21px * $k 54px * $k 40px * $k; }
	.camera_photos {
		width: 100px * $k;
		height: 100px * $k;
		&:before {
			font-size: 100px * $k; } }
	.camera_next {
		width: 100px * $k;
		height: 100px * $k;
		&:before {
			font-size: 50px * $k;
			line-height: 100px * $k; } }
	.camera_shot {
		display: inline-block;
		width: 100px * $k;
		height: 100px * $k;
		&:before {
			font-size: 46px * $k;
			line-height: 100px * $k; } }
	.photo_thumbs {
		padding-top: 45px * $k;
		.slick-dots {
			top: 20px * $k;
			li {
				margin: 0 10px * $k; }
			button {
				width: 10px * $k;
				height: 10px * $k;
				line-height: 10px * $k; } } }
	.photo_thumb {
		margin: 5px * $k;
		img {
			max-width: 162px * $k;
			max-height: 162px * $k; } }
	.checkout_form_header {
		height: 88px * $k;
		padding: 18px * $k 0 17px * $k 0;
		.back {
			margin-left: 40px * $k;
			&:before {
				font-size: 53px * $k; } }
		.close {
			margin: 5px * $k 20px * $k 5px * $k 0;
			&:before {
				font-size: 40px * $k; } } }
	.checkout_form {
		margin: 200px * $k 40px * $k 0;
		.buttons {
			margin-top: 60px * $k; }
		.button {
			width: 350px * $k;
			&:after {
				right: 38px * $k;
				margin-top: -25px * $k;
				font-size: 50px * $k; }
			&.done {
				&:after {
					display: none; } } }
		.price {
			margin-top: -15px * $k;
			&:before {
				content: '$';
				font-size: 72px * $k;
				top: -8px * $k; }
			.inputbox {
				font-size: 72px * $k;
				padding: 0 64px * $k 36px * $k;
				height: 93px * $k; } } }
	.checkout_form_title {
		font-size: 48px * $k;
		margin-bottom: 86px * $k; }
	.checkout_form_ico {
		width: 100px * $k;
		height: 100px * $k;
		margin: 0 auto 20px * $k;
		&.ico1 {
			&:before {
				font-size: 100px * $k; } }
		&.ico2 {
			&:before {
				font-size: 100px * $k; } }
		&.ico3 {
			&:before {
				font-size: 100px * $k; } } }

	.header {
		padding: 58px * $k 0 10px * $k; }
	.header_search {
		width: $smw;
		padding: 0 20px * $k; }

	.search_form {
		margin-right: 72px * $k;
		.inputbox {
			height: 60px * $k;
			padding: 10px * $k 40px * $k 10px * $k 140px * $k;
			line-height: 40px * $k;
			font-size: 30px * $k;
			@include rad(10px * $k); }
		label {
			height: 60px * $k;
			padding-left: 48px * $k;
			margin-left: -67px * $k;
			span {
				margin-top: 12px * $k;
				&:before {
					font-size: 39px * $k; }
				&:after {
					font-size: 22px * $k;
					margin-left: 3px * $k;
					top: -2px * $k; } }
			&:before {
				left: 10px * $k;
				margin-top: -10px * $k;
				font-size: 25px * $k; } } }
	.header_filter {
		right: 20px * $k;
		width: 50px * $k;
		height: 60px * $k;
		&:before {
			font-size: 40px * $k;
			line-height: 60px * $k; } }

	.footer {
		button {
			width: 100px * $k;
			height: 100px * $k;
			box-shadow: 0px 4px * $k 4px * $k 0px rgba(0, 0, 0, 0.4); }
		.menu {
			span {
				width: 44px * $k;
				height: 28px * $k;
				border: 1px solid #fff;
				border-left: none;
				border-right: none;
				&:before {
					border-top: 1px solid #fff;
					margin-top: 0; } } }
		.photo {
			&:before {
				font-size: 46px * $k;
				line-height: 100px * $k; } } }
	.footer_inn {
		width: $smw;
		padding: 0 20px * $k 20px * $k; }


	.grid {}
	.grid_gutter {
		width: 20px * $k; }
	.grid_sizer {
		width: 345px * $k; }
	.grid_item {
		width: 345px * $k;
		margin-top: 20px * $k; }
	.item_link {
		font-size: 24px * $k; }
	.item_img {}
	.item_foot {
		padding: 13px * $k 20px * $k; }
	.grid_title {
		font-size: 30px * $k;
		line-height: 36px * $k; }
	.grid_price {}
	.grid_model {
		&:before {
			margin: 0 15px * $k; } }
	.item-label {
		left: -24px * $k;
		top: 12px * $k;
		width: 100px * $k;
		font: bold #{20px * $k}/#{26px * $k} $ar; }

	.invite_inn {
		padding: 33px * $k 24px * $k; }
	.invite_close {
		top: 20px * $k;
		&:before {
			font-size: 35px * $k; } }
	.invite_text {
		width: 457px * $k;
		font-size: 24px * $k;
		line-height: 30px * $k; }
	.invite_button {
		width: 133px * $k;
		height: 50px * $k;
		@include rad(25px * $k);
		font-size: 24px * $k;
		margin-left: 19px * $k; } }













