@charset "UTF-8";
@import "mixin";
@import "normalize";


@include font('Sanchez','Sanchez-Regular',400);
@include font('Sanchez','Sanchez-Italic',400,italic);


$sn: 'Sanchez';
$ar: Arial, "Helvetica CY", "Nimbus Sans L", sans-serif;

$font: $sn, Arial, "Helvetica CY", "Nimbus Sans L", sans-serif;
$link: #fff;
$brand: #9f00ff;

$base-fz: 30px;
