$w: 750px;

$dblue: #421160;
$green: #66bc00;
$red: #f26c4f;
$grey1: #f1f1f1;
$grey2: #d2d2d2;
$grey3: #969696;
$grey4: #525252;
$grey5: #212121;



$icon-q: "\e90c";
