@font-face {
	font-family: 'icomoon';
	src: url('icomoon.ttf?1jt7tg') format('truetype'), url('icomoon.woff?1jt7tg') format('woff'), url('icomoon.svg?1jt7tg#icomoon') format('svg');
	font-weight: normal;
	font-style: normal; }

@mixin icon($icon) {
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	content: $icon; }
